@font-face {
    font-family: 'Montserrat';
    src: url('../fonts/Montserrat-Regular.ttf');
}

$font: Montserrat;
$black: #013557;
$white: #fff;
$blue: #113A93;
$green: #00B762;
$light-gray: #777;
$light-white: #f3f3f3;
$light-blue: #1148F2;

::selection {
    background-color: $black;
    color: $green;
}
body {
    font-family: $font;
    background-color: $light-white;
    color: $black;
}
hr {
    border: 1px solid $light-white;
    margin: 10px 0;
}

h1 {
    font-size: 35px !important;
}
h2 {
    font-size: 28px !important;
}
h3 {
    font-size: 20px !important;
}
h4 {
    font-size: 18px !important;
}
.card {
    border: none;
}

table {
    width: 100%;
    font-size: 15px;
    border-bottom: 2px solid $light-white;
    th, td {
        padding: 5px 10px;
    }
    th {
        background-color: $green;
        color: $white;
    }
    tr:nth-of-type(even) {
        background-color: $light-white;
    }
}

/* Syntax Highlighting */
.language-plaintext {
    color: $white;
    font-size: 15px;
    background-color: $green;
    border-radius: 5px;
    padding: 2px 3px;
}
.highlight {
    padding: 7px 7px 1px 7px;
    font-size: 15px;
    line-height: 20px;
    border-radius: 5px;
    background-color: $light-white;
    max-height: 400px;
    text-align: left;
    .lineno {
        font-size: 15px;
        color: $black;
        padding: 0 5px;
    }
    .code {
        font-size: 17px;
    }
}
/*-- Syntax Highlighting --*/

/* Smooth Scrolling */
html {
  scroll-behavior: smooth;
}
/* -- Smooth Scrolling -- */

/* Scroll to Top */
#toTop {
    padding: 10px;
    background-color: $light-blue;
    color: $white;
    bottom: 20px;
    right: 10px;
    z-index: 15;
    display: none;
    cursor: pointer;
    position: fixed;
    opacity: .75;
    -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=95)";
    border-radius: 50%;
    width: 45px;
    height: 45px;
    text-align: center;
}
    
#toTop:hover {
    opacity: 1;
}
/* -- Scroll to Top -- */

/* Search Style */
.search {
    section {
        margin: 10px 0;
        padding: 20px 20px 10px 20px;
        background-color: $white;
        border-radius: 5px;
        h3 {
            background-color: $light-white;
            border: 4px dotted $blue;
            padding: 15px;
            margin-bottom: 20px;
            border-radius: 10px;
            color: $blue;
            font-weight: bolder;
            span {
                display: inline-block;
                margin-right: 10px;
                padding: 5px 10px;
                background-color: $green;
                color: $white;
                border-radius: 5px;
            }
        }
        .search-content {
            display: flex;
            padding-bottom: 10px;

            img {
                width: 30%;
                margin-right: 20px;
            }
            p {
                width: 70%;
                line-height: 30px;
                a {
                    display: inline-block;
                    background-color: $green;
                    padding: 3px 10px;
                    font-size: 15px;
                    border-radius: 5px;
                    color: $white !important;
                }
            }
        }
    }
}
/*-- Search Style --*/

.post-content {
    display: flex;

    .card {
        width: 70%;
    }
    aside {
        width: 30%;
    }
}

/* Attention Footer */
.attention {
    color: $white;
    background-color: $green;
    padding: 10px 20px 0 20px;
    font-size: 13px;
    font-weight: bold;
}
/*-- Attention Footer --*/

@media screen and (max-width: 990px) {
    #toTop {
        bottom: 70px;
        right: 20px;
    }
    .search-content {
            img, p {
            width: 50% !important;
        }
    }
    .post-content {
        flex-direction: column;
        
        .card, aside {
            width: 100%;
        }
        aside {
            h4, .sidebar-image {
                margin-right: 0;
            }
            .sidebar-image {
                display: flex;
                align-items: center;
                
                .image {
                    width: 25%;

                    img {
                        width: 100% !important;
                    }
                }
                .image-desc {
                    width: 75%;
                    text-align: left;
                    padding-left: 20px;
                }
            }
        }
    }
}

@media screen and (max-width: 576px) {
    
    .search {
        section {
            flex-direction: column;
        }
        .search-content {
            flex-direction: column;
            img, p {
                width: 100% !important;
            }
            p {
                margin-top: 15px;
            }
        }
    }
    aside {
        .sidebar-image {
            flex-direction: column;
            
            .image {
                width: 40% !important;
            }
            .image-desc {
                width: 100%;
                text-align: center !important;
                padding-left: 0 !important;
            }

        }
    }
}